export default {
	props: {
		lostInfo: {
			type: Object,
			default() {
				return {}
			}
		}
	},
	data() {
		return {
			noDeleted: true
		}
	},
	methods: {
		showDetail() {
			this.$emit('showDetail', this.lostInfo._id)
		},
		deleteInfo() {
			if(this.$store.state.userInfo._openid === this.lostInfo._openid) {
				uni.showModal({
					title: '提示',
					content: '确定删除该条信息?',
					confirmText: '删除',
					success: res => {
						if(res.confirm) {
							console.log(this.lostInfo._id)
							wx.cloud.callFunction({
								name: 'findlost',
								data: {
									$url: 'delete',
									docid: this.lostInfo._id
								}
							}).then(res=> {
								if(res.result.stats.removed>0) {
									this.noDeleted = false
									uni.showToast({
										icon: 'none',
										title: '删除成功'
									})
								}
							})
						}
					}
				})
			}
		}
	}
}
